				"html": "<p>牢獄に入れられた２人の囚人<br>\nひとりは泥を見て<br>\nひとりは星を見た。  </p>\n<p>他人を羨む気持ちは泥，<br>\n自分の良いところに目を向けるのは星。  </p>\n<p>どんな人にも良い面，悪い面，<br>\n優れたところ，劣ったところはある。  </p>\n<p>羨望の眼差しを向けられる人にも<br>\n他者には分からない悩みはあるものだ。  </p>\n<p>自分の欠点や不利な点に注目するよりも<br>\n強みや有利な点を活かした方がよい。  </p>\n<p>星を見ることは，<br>\n人生をよりよく生きるための<br>\n秘訣のひとつだろう。  </p>\n<p>しかしながら，他者の良いところを見つけ，<br>\n引き出し，伸ばすこともまた星である。<br>\nあるいは，他者の悪い面を指摘して<br>\n扱き下ろしてばかりいては<br>\nよい結果をもたらさないだろう。<br>\n星のカタチはひとつではない。  </p>\n<p>見るべきものはモノではなく<br>\n自分の心の状態にある。  </p>\n<p>自分が良い気持ちになれることに<br>\nフォーカスすること。  </p>\n<p>だが，何によって気持ちがよくなるのかは<br>\n人によって異なる。  </p>\n<p>破壊や暴力，他者を貶めることで<br>\n快楽を得る者もいるかもしれない。<br>\n彼にとっては（邪悪な）星だが<br>\n世の中にとっては許容しがたいものである。  </p>\n<p>つまり，何を星とするべきなのか<br>\nということは，また別の問題なのである。  </p>\n<p>・・・</p>\n<p>では，<br>\n何を星と捉えることが望ましいのだろう。  </p>\n<p>それはコンテキストからもたらされる<br>\n道徳によって左右されている。<br>\nコンテキストとは，時代，土地，社会，宗教，文化，思想である。</p>\n<p>社会を重んじるべきか，家族が大切か。<br>\n集団の調和か，個人の自由か。<br>\n人種や性別はどのような違いをもたらすか<br>\nあるいはもたらすべきではないか。</p>\n<p>平和や持続可能性というキーワードも<br>\n昔は今ほど強調されてはいなかっただろう。  </p>\n<p>しかし，<br>\n同じ時代，同じ社会に生きる者であっても<br>\n必ずしも同じ道徳が適用される訳ではない。  </p>\n<p>コンテキストは<br>\n幾重にも重なる層のようになっている。<br>\nベースとなる大きなコンテキストの上に<br>\n個々の多様なコンテキストが折り重なって<br>\n混在している。  </p>\n<p>同じ時代の同じ国や地域であっても<br>\n異なる立場，異なる思想を持つ人たちで<br>\n構成されている。<br>\n実際に受ける影響は，接した人や<br>\n知りえた情報に依存する。<br>\n親，兄弟，友人，先生，仲間。<br>\n生まれ育って，接した人たちはみな異なる。  </p>\n<p>また，<br>\n受け手にもよっても受け取り方は異なる。<br>\n受け手の性質やレベルによって<br>\n様々な反応が生まれる。  </p>\n<p>受け手の性質に関しては，<br>\n主に遺伝的要素が強いと思われる。<br>\n血のつながりが近しいものは<br>\n基本的に似た傾向を示す。  </p>\n<p>しかしながら，兄弟であっても<br>\nその性質が大きく異なる場合もある。<br>\nこれは，絶妙な揺らぎが異なる可能性を<br>\n発現させているためだと思われる。<br>\n種としての多様性を生み出すためのもの<br>\nであろう。  </p>\n<p>受け手のレベルに関しては，<br>\nどの程度，またどのように<br>\n教育を受けたかによって<br>\n違いがもたらされる。  </p>\n<p>前提となる知識がなかったり，<br>\n理解できるように訓練されていなければ<br>\nより上位の概念を受容ことはできない。  </p>\n<p>さらにいえば，知識だけでなく<br>\n知性のレベルによっても異なる。  </p>\n<p>ロバート・キーガン教授によれば<br>\n知性には３つの段階があるという。<br>\n「環境順応型知性」<br>\n「自己主導型知性」<br>\nそして<br>\n「自己変容型知性」  </p>\n<p>第一段階の知性は<br>\n環境から望まれたものを読み取り，<br>\nあるべきカタチへと適合しようとする。<br>\n彼は与えられた課題をこなすだろう。  </p>\n<p>第二段階の知性は<br>\n自らの価値観において自律的に行動する。<br>\n自らを管理し，他者を導くことができる。<br>\n彼は自らこなすべき課題を設定するだろう。  </p>\n<p>そして<br>\n第三段階の知性は<br>\nあらゆるシステムが断片的で<br>\n不完全であると理解している。<br>\n１つの価値観だけでなく，<br>\n複数の視点や矛盾を受け入れられる。<br>\n彼はより大きな問題を解決するために<br>\n自らの知性をより高いレベルへと<br>\n変容させるだろう。  </p>\n<p>つまり<br>\n自分が影響を受けたコンテキストの<br>\n一側面を超える力。<br>\n自分が常識としていることを疑い，<br>\nその先にある真実を探求する力。  </p>\n<p>いま見えている星が全てではない。  </p>\n<p>本当に見るべき，<br>\n真なる星を探し続けけることはできるのだろうか。  </p>",
